  componentDidMount(){
    this.props.pubnub.getMessage(this.props.gameChannel, (msg) => {
      // Publish move to the opponent's board
      console.log("turn ", msg.message.turn, " player ", this.props.piece, ", action: ", msg.message.action);

      if (msg.message.action == "chat"){
        this.updateChat(msg.message.chat);

      }
      if (msg.message.action == "end turn"){
        this.newTurn(msg.message.turn);
      }
      if (msg.message.action == "trade" || msg.message.action == "endTrade"){
        this.updateBoard(msg.message.action, msg.message.piece, msg.message.players, msg.message.squares, msg.message.toasts, msg.message.deal);
      }
      if(this.turn != this.props.piece){
        if (msg.message.action == "move"){
          this.updateBoard(msg.message.action, msg.message.piece, msg.message.players, msg.message.squares, msg.message.toasts);
        }else if (["purchase", "pay", "upgrade","recruit","assault"].includes(msg.message.action)){
          this.updateBoard(msg.message.action, msg.message.piece, msg.message.players, msg.message.squares, msg.message.toasts, msg.message.deal);
        }else{
          console.log(msg.message);
        }
      }
      if (msg.message.toasts){
        for (let toast of msg.message.toasts){
          this.updateChat(toast);
        }
      }
      if (msg.message.action == "victory"){
        alert("A victor has emerged!")
      }
      
    });
  }

