import React from 'react';
import './Trader.css';

class Trader extends React.Component {
  // Create the trading interface
  constructor(props) {
    super(props)
    this.state = {
      dealMoney1: this.props.dealMoney1,
      dealMoney2: this.props.dealMoney2
    }
    this.money1SliderHandler = this.money1SliderHandler.bind(this);
    this.money2SliderHandler = this.money2SliderHandler.bind(this);
    this.propose = this.propose.bind(this);
    this.cancel = this.cancel.bind(this);
    this.counter = this.counter.bind(this);
    this.accept = this.accept.bind(this);
    this.decline = this.decline.bind(this);
  }
  propose() {
    let checkboxes = Array.from(document.getElementsByClassName("tileCheckBox"));
    let tiles = [];
    for (let checkbox of checkboxes){
      if (checkbox.checked){
        tiles.push(checkbox.id);
      }
    }
    this.props.submit({
      trader1: this.props.trader1,
      trader2: this.props.trader2,
      money1: this.state.dealMoney1,
      money2: this.state.dealMoney2,
      tiles: tiles,
      action: "propose"
    })
  }

  counter() {
    let checkboxes = Array.from(document.getElementsByClassName("tileCheckBox"));
    let tiles = [];
    for (let checkbox of checkboxes){
      if (checkbox.checked){
        tiles.push(checkbox.id);
      }
    }
    this.props.submit({
      trader1: this.props.trader1,
      trader2: this.props.trader2,
      money1: this.state.dealMoney1,
      money2: this.state.dealMoney2,
      tiles: tiles,
      action: "counter"
    })
  }

  decline() {
    this.props.submit({
      action: "decline"
    })
  }

  accept() {
    let dealMoney1 = document.querySelector("#money1").value;
    let dealMoney2 = document.querySelector("#money2").value;
    let checkboxes = Array.from(document.getElementsByClassName("tileCheckBox"));
    let tiles = [];
    for (let checkbox of checkboxes){
      if (checkbox.checked){
        tiles.push(checkbox.id);
      }
    }
    this.props.submit({
      trader1: this.props.trader1,
      trader2: this.props.trader2,
      money1: dealMoney1,
      money2: dealMoney2,
      tiles: tiles,
      action: "accept"
    })
  }

  cancel() {
    this.props.submit({
      action: "cancel"
    })
  }

  money1SliderHandler(e) {
    this.setState({dealMoney1: e.target.value});
  }

  money2SliderHandler(e) {
    this.setState({dealMoney2: e.target.value});
  }

  render() {
    let trade;
    let tradableLands1 = [];
    let tradableLands2 = [];

    if (this.props.piece == this.props.trader1 && this.props.phase == "propose"){

      for (let tileNum of this.props.territory1){
        let tile = this.props.squares[tileNum];
        tradableLands1.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num}/> {tile.name}</div>)
      }
      
      for (let tileNum of this.props.territory2){
        let tile = this.props.squares[tileNum];
        tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num}/> {tile.name}</div>)
      }

      trade = 
      <div className="tradeWrapper">
        <div className="tradeInfo">
          <div className="trader give">
            Player {this.props.trader1} gives:<br/>
            <input type="range" min="0" max={this.props.maxMoney1} step="10" value={this.state.dealMoney1} className="slider" id="money1" onChange={this.money1SliderHandler}/>
            <div className="sliderValue1">$ {this.state.dealMoney1}</div>
              {tradableLands1}
          </div>

          <div className="trader take">
            Player {this.props.trader2} gives:<br/>
              <input type="range" min="0" max={this.props.maxMoney2} step="10" value={this.state.dealMoney2} className="slider" id="money2" onChange={this.money2SliderHandler}/>
              <div className="sliderValue2">$ {this.state.dealMoney2}</div>
              {tradableLands2}
          </div>
        </div>

        <button onClick={this.propose}>Propose</button>
        <button onClick={this.cancel}>Cancel</button>
      </div>
        
    }else if (this.props.piece == this.props.trader2 && this.props.phase == "counter"){

      for (let tileNum of this.props.territory1){
        let tile = this.props.squares[tileNum];
        if (this.props.dealTiles.includes(tile.num.toString())){
          tradableLands1.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} checked/> {tile.name}</div>)
        }else {
          tradableLands1.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num}/> {tile.name}</div>)
        }
      }
      
      for (let tileNum of this.props.territory2){
        let tile = this.props.squares[tileNum];
        if (this.props.dealTiles.includes(tile.num.toString())){
          tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} checked/> {tile.name}</div>)
        }else {
          tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num}/> {tile.name}</div>)
        }
      }

      trade = 
      <div className="tradeWrapper">
        <div className="tradeInfo">
          <div className="trader give">
            Player {this.props.trader1} gives:<br/>
            <input type="range" min="0" max={this.props.maxMoney1} step="10" value={this.state.dealMoney1}  className="slider" id="money1" onChange={this.money1SliderHandler}/>
            <div className="sliderValue1">$ {this.state.dealMoney1}</div>
              {tradableLands1}
          </div>

          <div className="trader take">
            Player {this.props.trader2} gives:<br/>
              <input type="range" min="0" max={this.props.maxMoney2} step="10" value={this.state.dealMoney2}  className="slider" id="money2" onChange={this.money2SliderHandler}/>
              <div className="sliderValue2">$ {this.state.dealMoney2}</div>
              {tradableLands2}
          </div>
        </div>

        <button onClick={this.accept}>Accept</button>
        <button onClick={this.counter}>Propose</button>
        <button onClick={this.decline}>Decline</button>
      </div>

    }else if (this.props.piece == this.props.trader1 && this.props.phase == "confirm"){

      for (let tileNum of this.props.territory1){
        let tile = this.props.squares[tileNum];
        if (this.props.dealTiles.includes(tile.num.toString())){
          tradableLands1.push(<div><input type="checkbox"  className="tileCheckBox" id={tile.num} checked disabled/> {tile.name}</div>)
        }else {
          tradableLands1.push(<div><input type="checkbox"  className="tileCheckBox" id={tile.num} disabled/> {tile.name}</div>)
        }
      }
      
      for (let tileNum of this.props.territory2){
        let tile = this.props.squares[tileNum];
        if (this.props.dealTiles.includes(tile.num.toString())){
          tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} checked disabled/> {tile.name}</div>)
        }else {
          tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} disabled/> {tile.name}</div>)
        }
      }

      trade = 
      <div className="tradeWrapper">
        <div className="tradeInfo">
          <div className="trader give">
            Player {this.props.trader1} gives:<br/>
            <input type="range" min="0" max={this.props.maxMoney1} step="10" value={this.state.dealMoney1} className="slider" id="money1" disabled/>
            <div className="sliderValue1">$ {this.state.dealMoney1}</div>
              {tradableLands1}
          </div>

          <div className="trader take">
            Player {this.props.trader2} gives:<br/>
              <input type="range" min="0" max={this.props.maxMoney2} step="10" value={this.state.dealMoney2} className="slider" id="money2" disabled/>
              <div className="sliderValue2">$ {this.state.dealMoney2}</div>
              {tradableLands2}
          </div>
        </div>

        <button onClick={this.accept}>Accept</button>
        <button onClick={this.decline}>Decline</button>
      </div>

    }else{

      for (let tileNum of this.props.territory1){
        let tile = this.props.squares[tileNum];
        if (this.props.dealTiles.includes(tile.num.toString())){
          tradableLands1.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} checked disabled/> {tile.name}</div>)
        }else {
          tradableLands1.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} disabled/> {tile.name}</div>)
        }
      }
      
      for (let tileNum of this.props.territory2){
        let tile = this.props.squares[tileNum];
        if (this.props.dealTiles.includes(tile.num.toString())){
          tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} checked disabled/> {tile.name}</div>)
        }else {
          tradableLands2.push(<div><input type="checkbox" className="tileCheckBox" id={tile.num} disabled/> {tile.name}</div>)
        }
      }

      trade = 
      <div className="tradeWrapper">
        <div className="tradeInfo">
          <div className="trader give">
            Player {this.props.trader1} gives:<br/>
            <input type="range" min="0" max={this.props.maxMoney1} step="10" value={this.state.dealMoney1} className="slider" id="money1" disabled/>
            <div className="sliderValue1">$ {this.state.dealMoney1}</div>
              {tradableLands1}
          </div>

          <div className="trader take">
            Player {this.props.trader2} gives:<br/>
              <input type="range" min="0" max={this.props.maxMoney2} step="10" value={this.state.dealMoney2} className="slider" id="money2" disabled/>
              <div className="sliderValue2">$ {this.state.dealMoney2}</div>
              {tradableLands2}
          </div>
        </div>

      </div>
    }

    
    return (
      <div>
        {this.props.phase} phase
        <br></br>
        {trade}
      </div>
    )
  }
}

export default Trader;