// class design for warlords
// a monopoly game, with additional combat feature like risk
// player can purchase an un-owned land and build hotels to charge rent on passing-by opponents
// instead of paying rent to your opponent, you can fight his garrison with your troops
// winning the combat can convert the ownership of this land to you!
// losing combat will cause player to lose half his troops
// players can initiate deals with another player, who can counter offer once.
// They can trade money, for land, and even temporary truce.
// Player passing by owned land can use money to upgrade the city when he passes by, boosting rent and defensive power.
// he can also recruit troops with money

class tile {
  constructor(num, name, rents, garrisons, upgradePrice=50) {
      this.num = num;
      this.name = name;
      this.owner = null;
      this.rents = rents || [10, 20, 40, 80];
      this.garrisons = garrisons || [4, 5, 6, 8];
      this.upgradePrice = upgradePrice;
      this.level = 0; //can charge rent from opponent, can defend itself, can be upgraded
      this.multiplier = 1;
  }
}

class player {
  constructor(num) {
      this.number = num;
      this.army = 1;
      this.money = 1500;
      this.territory = []; //owned tiles
      this.position = 0;
      this.railsOwned = 0;
  }
}

export {tile, player};