import React, { Component } from 'react';
import Game from './Game';
import Board from './Board';
import PubNubReact from 'pubnub-react';
import Swal from "sweetalert2";  
import shortid  from 'shortid';
import './Game.css';
 
class App extends Component {
  constructor(props) {  
    super(props);
    this.pubnub = new PubNubReact({
      publishKey: "pub-c-7ff08bc5-f641-4297-aef9-00f08ecdd561", 
      subscribeKey: "sub-c-a408ea2a-1236-11ea-8755-faba8a6641c7",
      uuid:parseInt(Math.random()*100000).toString(10)
    });
    
    this.state = {
      piece: '',
      isPlaying: false,
      isRoomCreator: false,
      isDisabled: false,
      myTurn: false,
    };

    this.lobbyChannel = null;
    this.gameChannel = null;
    this.roomId = null;    
    this.pubnub.init(this);
  }  
  
  componentWillUnmount() {
    this.pubnub.unsubscribe({
      channels : [this.lobbyChannel, this.gameChannel]
    });
  }
  
  componentDidUpdate() {
    // Check that the player is connected to a channel
    if(this.lobbyChannel != null){
      this.pubnub.getMessage(this.lobbyChannel, (msg) => {
        if(msg.message.notRoomCreator){
          // Create a different channel for the game
          this.gameChannel = 'mafiaGame--' + this.roomId;

            this.pubnub.subscribe({
              channels: [this.gameChannel]
            });
  
            this.setState({
              isPlaying: true
            });  
  
            // Close the modals if they are opened
            Swal.close();
          
        }
      }); 
    }
  }

  // Create a room channel
  onPressCreate = (e) => {
    // Create a random name for the channel
    this.roomId = parseInt(Math.random()*100000).toString(10);
    this.lobbyChannel = 'mafiaLobby--' + this.roomId;

    this.pubnub.subscribe({
      channels: [this.lobbyChannel],
      withPresence: true
    });

  // Open the modal
  Swal.fire({
    position: 'top',
    allowOutsideClick: false,
    title: 'Share this room ID with your friend',
    text: this.roomId,
    width: 275,
    padding: '0.7em',
    // Custom CSS
    customClass: {
        heightAuto: false,
        title: 'title-class',
        popup: 'popup-class',
        confirmButton: 'button-class'
    }
  })

    this.setState({
      piece: '1',
      isRoomCreator: true,
      isDisabled: true, // Disable the 'Create' button
      myTurn: true, // Room creator makes the 1st move
    });   
  }
  
  // The 'Join' button was pressed
  onPressJoin = (e) => {
    Swal.fire({
      position: 'top',
      input: 'text',
      allowOutsideClick: false,
      inputPlaceholder: 'Enter the room id',
      showCancelButton: true,
      confirmButtonColor: 'blue',
      confirmButtonText: 'OK',
      width: 275,
      padding: '0.7em',
      customClass: {
        heightAuto: false,
        popup: 'popup-class',
        confirmButton: 'join-button-class '
      } 
    }).then((result) => {
      // Check if the user typed a value in the input field
      if(result.value){
        this.joinRoom(result.value);
      }
    })
  }

  // Join a room channel
  joinRoom = (value) => {
    this.roomId = value;
    this.lobbyChannel = 'mafiaLobby--' + this.roomId;

    // Check the number of people in the channel
    this.pubnub.hereNow({
      channels: [this.lobbyChannel], 
    }).then((response) => {
        alert("Current players in the room: "+response.totalOccupancy);

        if(response.totalOccupancy < 4){
          this.pubnub.subscribe({
            channels: [this.lobbyChannel],
            withPresence: true
          });
          
          this.setState({
            piece: (1+response.totalOccupancy).toString(),
          });  
          
          this.pubnub.publish({
            message: {
              notRoomCreator: true,
            },
            channel: this.lobbyChannel
          });
        } 
        else{
          // Game in progress
          Swal.fire({
            position: 'top',
            allowOutsideClick: false,
            title: 'Error',
            text: 'Game in progress. Try another room.',
            width: 275,
            padding: '0.7em',
            customClass: {
                heightAuto: false,
                title: 'title-class',
                popup: 'popup-class',
                confirmButton: 'button-class'
            }
          })
        }
    }).catch((error) => { 
      console.log(error);
    });
  }

  // Reset everything
  endGame = () => {
    this.setState({
      piece: '',
      isPlaying: false,
      isRoomCreator: false,
      isDisabled: false,
      myTurn: false,
    });

    this.lobbyChannel = null;
    this.gameChannel = null;
    this.roomId = null;  

    this.pubnub.unsubscribe({
      channels : [this.lobbyChannel, this.gameChannel]
    });
  }
  
  render() {  
    return (  
        <div> 
          {/* <div className="title">
            Mafia Wars
          </div> */}
          {
            !this.state.isPlaying &&
            <div className="game">
              <div className="button-container">
                  <p>This is Mafia Wars! </p>
                  <p>It's a monopoly game in which you can hire gangsters and take other players' lands by force! </p>
                  <p>The combat mechanism is like Risk, You fight with "gangster" number of dices against "garrisons" number of dices, the one with more total points wins!</p>
                  <button 
                    className="create-button "
                    disabled={this.state.isDisabled}
                    onClick={(e) => this.onPressCreate()}
                    > Create 
                  </button>
                  <button 
                    className="join-button"
                    onClick={(e) => this.onPressJoin()}
                    > Join 
                  </button>
                  <p>How to start a game: </p>
                  <p>1 player creates the room, the game will appear when another player joins.</p>
                  <p>If there are more than 2 players, please join room one player at a time to avoid crashes. </p>
                  <p>Refreshing the page will end the game.</p>
                  <p>To restart, everyone needs to refresh and start over from this page.</p>
                  <p>You can open another browser tab and join as another player.</p>
                </div>
            </div>
          }

          {
            this.state.isPlaying &&
            <Game 
              roomId={this.roomId}
              pubnub={this.pubnub}
              gameChannel={this.gameChannel} 
              piece={this.state.piece}
              isRoomCreator={this.state.isRoomCreator}
              endGame={this.endGame}
            />
          }
        </div>
    );  
  } 
}

export default App;
