import React from 'react';
import Square from './Square';
import classnames from 'classnames';
import './Board.css';
import Game from './Game.js';


class Board extends React.Component {
  // Create the board
  constructor(props) {
    super(props)
    this.playerColors = [null, "redText", "blueText", "purpleText", "greenText"];
    this.playerBorders = [null, "redBorder", "blueBorder", "purpleBorder", "greenBorder"];
  }
  createBoard() {
    const board = [];
    board.push(<div className={classnames('center', this.playerBorders[this.props.piece])}></div>);
    let corner = this.renderSquare(0, "corner");
    board.push(corner);
    let row = [];
    for (let i=1;i<10;i++){
      row.push(this.renderSquare(i));
    }
    board.push(<div className="row horizontal-row top-row">{row}</div>);

    corner = this.renderSquare(10, "corner");
    board.push(corner);
    row = [];
    for (let i=11;i<20;i++){
      row.push(this.renderSquare(i));
    }
    board.push(<div className="row vertical-row right-row">{row}</div>);

    corner = this.renderSquare(30, "corner");
    board.push(corner);
    row = [];
    for (let i=29;i>20;i--){
      row.push(this.renderSquare(i));
    }
    board.push(<div className="row horizontal-row bottom-row">{row}</div>);

    corner = this.renderSquare(20, "corner");
    board.push(corner);
    row = [];
    for (let i=39;i>30;i--){
      row.push(this.renderSquare(i));
    }
    board.push(<div className="row vertical-row left-row">{row}</div>);

    return board;
  }
  
  renderSquare(i, className) {
    let ownerColor = "white";
    if (this.props.squares[i] && this.props.squares[i].owner){
      let owner = this.props.squares[i].owner;
      ownerColor = this.playerColors[owner[owner.length-1]];
    }
    
    return (
      <Square
        key={i}
        id={"square"+i}
        num={i}
        name={this.props.squares[i] ? this.props.squares[i].name : null}
        multiplier={this.props.squares[i] ? this.props.squares[i].multiplier : 1}
        special={this.props.specials.includes(i)}
        buyPrice={this.props.buyPrices[i]}
        styleName={className}
        provinceColor={this.props.provinces[i]}
        playerColor={ownerColor}
        owner={this.props.squares[i] ? this.props.squares[i].owner : null}
        rents={this.props.squares[i] ? this.props.squares[i].rents : null}
        garrisons={this.props.squares[i] ? this.props.squares[i].garrisons : null}
        upgradePrice={this.props.squares[i] ? this.props.squares[i].upgradePrice : null}
        level={this.props.squares[i] ? this.props.squares[i].level : null}
        visitors={this.props.squares[i] ? this.props.squares[i].visitors : null}
      />
    );
  }

  render() {
    return <div onClick={this.showDetail}>{this.createBoard()}</div>;
  }
}

export default Board;
